import React, { Component } from 'react';

import AppTitle from './../app-title';
import SearchPanel from './../search-panel';
import TodoList from './../todo-list';
import SearchPanel1 from './../search-panel-1';
import TodoButton from './../todo-button';
// css
import s from './app.module.css';

export default class App extends Component {
state={ todoList: [
    {id: 1, text: 'first todo', checked: true},
    {id: 2, text: 'second todo', checked: true},
    {id: 3, text: 'third todo', checked: false}
] }

 delItem= (id)=>{
    
    this.setState(({todoList})=>{
        const idx = todoList.findIndex((item)=> id=== item.id);

        const before = todoList.slice(0,idx);
        const after = todoList.slice(idx + 1);
        //const newTodoList =before.concat(after)
        //const newTodoList=[...before,...after]
        const newTodoList=[
            ...before.slice(0,idx),
            ...after.slice(idx + 1)]
    return {todoList:newTodoList};
    })
    // setImportant = (id) =>{
    //     class Square extends React.Component {
    //         constructor(props) {
    //           super(props);
    //           this.state = {
    //             value: null,
    //           };
    //         }
    //     }
    // }
}
    render(){
        
        return (
            <div className={s.app}>
                <AppTitle />
                <SearchPanel />
                <TodoList todos={this.state.todoList} 
                onDelItem= {this.delItem}
                />
                <SearchPanel1/>
                <TodoButton/>
            </div>
        );
    }
 
}

