import React, { Component } from 'react';

// css
import classes from './todo-item.module.scss';

export default class TodoItem extends Component {
    state = {
        done: false,
        important: false
    };

    itemDone = () =>{
        this.setetSate((done)=>{
          return{
            done: !done
          }  
        });
    };
    
 

    render() {
        const {text,onDelItem} = this.props;
        const {done, important} = this.state;
        const {todoItem, todoItemDone,  todoItemImportant, itemText} = classes;
        let itemClases = todoItem;

       
        if (done){
            itemClases = ` ${todoItemDone}`; 
        }

        if (important){
            itemClases = ` ${todoItemImportant}`; 
        }

        return (
        <li 
            className={todoItem + ' ' + todoItemDone}  
        >
            <span onClick={this.itemDone}  className={itemText}>{text}</span>
            <button onClick={this.setImportant}>i</button>
            <button onClick={onDelItem}>bel</button>
        </li>
        );
    }
}

